import React from "react";
import { Filtersbtn } from "../FiltersBtn/FiltersBtn";

export const Filters = () => {

    const filtersData = [
        {label: 'all', name: 'Все', id: 1},
        {label: 'meat', name: 'Мясные', id: 2},
        {label: 'vegetarian', name: 'Вегетарианская', id: 3},
        {label: 'grill', name: 'Гриль', id: 4},
        {label: 'sharp', name: 'Острые', id: 5},
        {label: 'closed', name: 'Закрытые', id: 6}
    ]

    const onToggleFilter = (label) => {
        console.log(label);
    }

    return (
        <ul className="filters list-reset">
            {filtersData.map(btn => {
                return (
                    <li className="filters__item" key={btn.id}>
                        <Filtersbtn btn={btn} key={btn.id} onClickFilter={onToggleFilter} />
                    </li>
                )
            })}
        </ul>
    )
}