import React from "react";
import { Product } from "../Product/Product";

const productsData = [
    {title: 'Чизбургер-пицца', price: 395, imgUrl: 'product-1.jpg', id: 1},
    {title: 'Сырная', price: 450, imgUrl: 'product-2.jpg', id: 2},
    {title: 'Креветки по-азиатски', price: 290, imgUrl: 'product-3.jpg', id: 3},
    {title: 'Сырный цыпленок', price: 385, imgUrl: 'product-4.jpg', id: 4},
    {title: 'Чизбургер-пицца', price: 395, imgUrl: 'product-1.jpg', id: 5},
    {title: 'Сырная', price: 450, imgUrl: 'product-2.jpg', id: 6},
    {title: 'Креветки по-азиатски', price: 290, imgUrl: 'product-3.jpg', id: 7},
    {title: 'Сырный цыпленок', price: 385, imgUrl: 'product-4.jpg', id: 8},
]

export const ProductList = ({number}) => {
    return (
        <ul className="products__list list-reset">
            {productsData.map(prod => {
                return (
                    <li className="product__item" key={prod.id}>
                        <Product prod={prod} key={prod.id} number={number} />
                    </li>
                )
            })}
        </ul>
    )
}